//      
import { UPDATE_MODULES } from '../types/modules';

//   
const initialState = {
  modules: [],
};

//     
const modulesReducer = (state = initialState, { type, payload }) => {
  //   
  switch (type) {
    case UPDATE_MODULES: {
      return {
        ...state,
        modules: [...payload],
      };
    }
    //    ,   
    default: {
      return state;
    }
  }
};

export default modulesReducer;
